/* Global
================================================================*/
/*font-family: 'Open Sans', sans-serif;
font-family: 'Lato', sans-serif;*/
:root {
  --main-font-color: #444;
  --primary-color: #015aaa;
  --secondary-color: #ffd10a;
  --main-button-color: #015aaa;
  --main-bg-color: #fff ;
  --main-link-color: #015aaa; }
html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  background: var(--main-bg-color) var(--main-bg-image) no-repeat center center fixed;
  background-size: cover; }
@media screen and (max-width: 992px) {
  html, body {
    background: var(--main-bg-color) var(--main-bg-image) no-repeat top center;
    background-size: auto;
    height: auto; } }
body, p {
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  font-weight: 200;
  line-height: 160%;
  color: #555; }
.container {
  background: rgba(255, 255, 255, 0.9);
  padding: 0 30px 30px; }
.no-gutter > [class*='col-'] {
  padding-right: 0;
  padding-left: 0; }
.relative {
  position: relative; }
.pad10 {
  padding: 10px; }
.pad20 {
  padding: 20px; }
.pad30 {
  padding: 30px; }
.pad40 {
  padding: 40px; }
.pad50 {
  padding: 50px; }
.pad60 {
  padding: 60px; }
.pad80 {
  padding: 80px; }
.padtb20 {
  padding: 20px 0; }
.padding6040 {
  padding: 60px 40px; }
.mtb10 {
  margin: 10px 0; }
.mtb20 {
  margin: 20px 0; }
.mb20 {
  margin: 0 0 20px; }
.mb40 {
  margin: 0 0 40px; }
.mb60 {
  margin: 0 0 60px; }
.mb100 {
  margin: 0 0 100px; }
.border-top {
  border-top: 1px dotted #ccc; }
.border-bottom {
  border-bottom: 1px dotted #ccc; }
.border-left {
  border-left: 1px dotted #ccc; }
.border-right {
  border-right: 1px dotted #ccc; }
/* Fonts
------------------------------------------------*/
h1, h2, h3, h4, h5, h6 {
  font-family: 'Lato', sans-serif;
  font-weight: 400;
  letter-spacing: .04em;
  color: #2e2e2e;
  text-transform: uppercase; }
h1 {
  font-size: 24px;
  font-weight: 700;
  margin: 0;
  color: #2e2e2e; }
h1 span {
  color: var(--primary-color);
  font-size: 18px;
  font-weight: 300; }
h2 {
  background: var(--primary-color);
  color: #fff;
  padding: 20px;
  margin: 0;
  font-size: 16px; }
h3 {
  font-size: 18px;
  line-height: 140%;
  font-weight: 400;
  margin: 0; }
h4 {
  font-weight: 600;
  margin: 0 0 20px; }
.txt12 {
  font-size: 12px;
  line-height: 120%; }
.txt14 {
  font-size: 14px;
  line-height: 120%; }
.txt18 {
  font-size: 18px;
  line-height: 100%; }
.white {
  color: #fff; }
.dk-grey {
  color: #444; }
.bg-grey {
  background: #f5f6f7; }
a,
a:hover,
a:visited,
a:focus {
  color: var(--main-link-color);
  text-decoration: none; }
.btn {
  border-radius: 0;
  font-family: 'Montserrat', sans-serif;
  color: #fff;
  border: none;
  text-transform: uppercase;
  padding: 15px 30px; }
.btn-main,
.btn-main:visited {
  background: var(--main-button-color);
  font-size: 14px;
  color: #fff; }
.btn-main:hover,
.btn-main:focus {
  background: #000;
  color: #fff; }
.btn-secondary,
.btn-secondary:visited {
  background: var(--main-button-color);
  font-size: 12px;
  padding: 8px 10px;
  color: #fff; }
img.ir {
  max-width: 100%;
  height: auto; }
input, select, textarea,
.input-group input,
.form-control {
  border-radius: 0;
  height: 44px;
  margin: 0 0 15px; }
textarea.form-control {
  height: 200px; }
.sm-field {
  width: 130px; }
label {
  font-size: 12px;
  text-transform: uppercase;
  margin-bottom: 0; }
input[type="checkbox"] {
  width: 20px;
  height: 20px;
  margin: 0; }
/* Login
----------------------------------------------*/
.login-form,
.register-form,
.disclaimer {
  max-width: 400px;
  padding: 50px;
  margin: 0 auto 80px;
  background: #fff;
  border: 1px solid #eaeaea;
  -webkit-box-shadow: 0px 0px 5px #ccc;
          box-shadow: 0px 0px 5px #ccc; }
.register-form,
.disclaimer {
  max-width: 700px; }
.disclaimer-content {
  height: 500px;
  padding: 0 20px 20px 0;
  overflow-y: scroll; }
.logo-top {
  padding: 140px 0 50px 0; }
/* Header
----------------------------------------------*/
header {
  padding: 20px 0 0;
  border-bottom: 1px dotted #ccc; }
.navigation a {
  font-size: 24px;
  margin: 0 0 0 25px; }
.date {
  position: absolute;
  right: 0;
  width: 250px; }
@media screen and (max-width: 992px) {
  .date {
    position: relative;
    width: auto; }
  .company-logo, .patient-name {
    text-align: center;
    margin: 0 0 10px; }
  header .text-right {
    text-align: left; }
  .header-right {
    margin: 0 10%; } }
@media screen and (max-width: 767px) {
  header .text-right {
    text-align: center; }
  .date {
    width: 280px;
    margin: 0 auto; } }
.day-nav {
  position: absolute;
  right: 15px;
  top: 20px; }
.day-nav a {
  padding: 5px 10px;
  color: #fff;
  font-size: 14px; }
.day-nav a.prev {
  background: #444; }
.day-nav a.next {
  background: var(--main-button-color); }
.breakfast,
.lunch,
.dinner {
  min-height: 350px; }
.meal-times .list-unstyled {
  min-height: 300px; }
.meal-times .ion-ios-list {
  position: absolute;
  right: 35px;
  top: 17px;
  font-size: 24px; }
.nutrition {
  width: 100%;
  height: 80px;
  overflow-x: scroll; }
.nutrition-container {
  width: 150%; }
.nutrition-item {
  position: relative;
  display: inline-block;
  width: 120px;
  background: #2e2e2e;
  font-size: 12px;
  color: #fff;
  text-transform: uppercase;
  border: 1px solid #e2e0e0;
  margin: 10px 1px 0 0;
  padding: 5px;
  text-align: center; }
.nutrition-item span {
  color: #f9f87c; }
.dislikes {
  background: #e6e9ed;
  border: 1px solid #ccc;
  font-size: 14px;
  text-transform: uppercase; }
.dislikes .ion-asterisk {
  color: #d6565b; }
.dislikes .ion-android-sad {
  font-size: 16px; }
.dislikes a {
  color: #444; }
.dislikes a:hover {
  background: #ccc; }
@media screen and (max-width: 992px) {
  .day-nav {
    position: relative;
    right: auto;
    top: auto;
    padding: 20px 0; }
  .nutrition-container {
    width: 300%; }
  .meal-times .pad20 {
    padding: 20px 5px; }
  .dislikes {
    font-size: 12px; }
  .dislikes .ion {
    display: none; } }
/* Modals
--------------------------*/
.modal-dialog {
  background: #fff; }
#no-delete .modal-dialog,
#no-delete-facilities .modal-dialog {
  background: #f6f2b4; }
.modal-body {
  border: none;
  border-radius: 0;
  padding: 70px 40px 40px;
  color: #000; }
.modal-body p {
  color: #444; }
.modal-body h4 {
  margin: 0 0 5px; }
.modal-body h4 span {
  font-size: 14px;
  color: var(--primary-color); }
.modal-body .ion {
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 30px;
  color: var(--primary-color);
  cursor: pointer; }
.modal-actions {
  padding: 20px 0 0;
  margin: 10px 0 0;
  border-top: 1px dotted #ccc; }
.family-member a {
  position: absolute;
  right: 0;
  top: 15px; }
#dislikes .tab-content {
  color: white;
  background-color: var(--primary-color);
  padding: 5px 15px; }
.nav-pills > li {
  float: left;
  width: 48%;
  text-align: center;
  background: #eaeaea;
  margin: 0 0 20px; }
.nav-pills > li > a {
  border-radius: 0;
  text-transform: uppercase;
  letter-spacing: .08em; }

.vdp-datepicker {
  position: absolute;
}
